
import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { v4 as uuidv4 } from 'uuid';
import { FilterContainer, FilterModal, FilterSlider } from '../../components/filter';
import { RadioDD, CheckboxDD, SearchCheckboxDD } from '../../components/customDropdown';
import Loader from '../../components/loader/Loader';
import Comment from '../../components/comment/Comment';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import { comments } from '../../constants/comments';
import {
  optionRange, optionGAM, optionGroupMethodology, optionImport,
  optionExport, optionCountryExp, optionVertical2, optionSTAR2, optionTerritory, optionChannelInfluence,
  optionFlag, optionCountry, optionMetro, optionCompanySize, optionHyperscaler, optionRegion, optionLighthouseFlag
} from '../../constants/filterOptions';
import CustomerContent from './CustomerContent';


const Export = () => {
  const [isLoading, setIsLoading] = useState(true)
  const [customerData, setData] = useState()
  const [idState, setReferenceID] = useState(null);
  const [modalShow, setModalShow] = useState(false);
  const [modalData, setModalData] = useState(null);
  const [resultData, setResultData] = useState(null);
  const [activeTab, setActiveTab] = useState('export');
  const [exportData, setExportData] = useState([]);
  const [importData, setImportData] = useState([]);
  const [countryData, setCountryData] = useState([]);

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true)
  }
  const onCloseFilter = async () => {

    await onCancelRange();

    setShowFilter(false);
  }
  const onApplyFilter = async () => {

    await onApplyRange();

    setShowFilter(false);
  }

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  };
  const onShowComment = () => {
    setShowComment(true);
  };

  // radio for import values
  const import_initial_value = optionImport ? optionImport[0] : null;
  const [import_val_onApply, set_import_val_onApply] = useState(import_initial_value);
  const [import_val, set_import_val] = useState(import_initial_value);
  const onApplyImport = () => {
    set_import_val_onApply(import_val);
  };
  const onCancelImport = () => {
    set_import_val(import_val_onApply);
  };

  // radio for export values
  const export_initial_value = optionExport ? optionExport[0] : null;
  const [export_val_onApply, set_export_val_onApply] = useState(export_initial_value);
  const [export_val, set_export_val] = useState(export_initial_value);
  const onApplyExport = () => {
    set_export_val_onApply(export_val);
  };
  const onCancelExport = () => {
    set_export_val(export_val_onApply);
  };


  // radio for methodology values
  const methodology_initial_value = optionGroupMethodology ? optionGroupMethodology[0] : null;
  const [methodology_val_onApply, set_methodology_val_onApply] = useState(methodology_initial_value);
  const [methodology_val, set_methodology_val] = useState(methodology_initial_value);
  const onApplyMethodology = () => {
    set_methodology_val_onApply(methodology_val);
  };
  const onCancelMethodology = () => {
    set_methodology_val(methodology_val_onApply);
  };

  // radio for gam values
  const gam_initial_value = optionGAM ? optionGAM[0] : null;
  const [gam_val_onApply, set_gam_val_onApply] = useState(gam_initial_value);
  const [gam_val, set_gam_val] = useState(gam_initial_value);
  const onApplyGAM = () => {
    set_gam_val_onApply(gam_val);
  };
  const onCancelGAM = () => {
    set_gam_val(gam_val_onApply);
  };

  // radio for range values
  const range_initial_value = optionRange ? optionRange[0] : null;
  const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
  const [range_val, set_range_val] = useState(range_initial_value);
  const onApplyRange = () => {
    set_range_val_onApply(range_val);
  };
  const onCancelRange = () => {
    set_range_val(range_val_onApply);
  };

  // checkbox for vertical values
  const [vertical_val_onApply, set_vertical_val_onApply] = useState([]);
  const [vertical_val, set_vertical_val] = useState([]);
  const onApplyVertical = () => {
    set_vertical_val_onApply(vertical_val);
  };
  const onCancelVertical = () => {
    set_vertical_val(vertical_val_onApply);
  };

  // radio for hyperscaler values
  const hyperscaler_initial_value = optionHyperscaler ? optionHyperscaler[0] : null;
  const [hyperscaler_val_onApply, set_hyperscaler_val_onApply] = useState(hyperscaler_initial_value);
  const [hyperscaler_val, set_hyperscaler_val] = useState(hyperscaler_initial_value);
  const onApplyHyperscaler = () => {
    set_hyperscaler_val_onApply(hyperscaler_val);
  };
  const onCancelHyperscaler = () => {
    set_hyperscaler_val(hyperscaler_val_onApply);
  };

  // searchCheckbox for metro values
  const [metro_val, set_metro_val] = useState([]);
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val);
  };
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply);
  };



  // checkbox for hqregion values
  const [hqregion_val_onApply, set_hqregion_val_onApply] = useState([]);
  const [hqregion_val, set_hqregion_val] = useState([]);
  const onApplyHQRegion = () => {
    set_hqregion_val_onApply(hqregion_val);
  };
  const onCancelHQRegion = () => {
    set_hqregion_val(hqregion_val_onApply);
  };


  // checkbox for hq region values
  const [region_val, set_region_val] = useState([]);
  const [region_val_onApply, set_region_val_onApply] = useState([]);
  const onApplyRegion = () => {
    set_region_val_onApply(region_val);
  };
  const onCancelRegion = () => {
    set_region_val(region_val_onApply);
  };


  // searchCheckbox for hq country values
  const [hqcountry_val_onApply, set_hqcountry_val_onApply] = useState([]);
  const [hqcountry_val, set_hqcountry_val] = useState([]);
  const onApplyHQCountry = () => {
    set_hqcountry_val_onApply(hqcountry_val);
  };
  const onCancelHQCountry = () => {
    set_hqcountry_val(hqcountry_val_onApply);
  };

  // radio for flag values
  const flag_initial_value = optionFlag ? optionFlag[0] : null;
  const [flag_val_onApply, set_flag_val_onApply] = useState(flag_initial_value);
  const [flag_val, set_flag_val] = useState(flag_initial_value);
  const onApplyFlag = () => {
    set_flag_val_onApply(flag_val);
  };
  const onCancelFlag = () => {
    set_flag_val(flag_val_onApply);
  };

  // searchCheckbox for hq country values
  const [country_val_onApply, set_country_val_onApply] = useState([]);
  const [country_val, set_country_val] = useState([]);
  const onApplyCountry = () => {
    set_country_val_onApply(country_val);
  };
  const onCancelCountry = () => {
    set_country_val(country_val_onApply);
  };

  // checkbox for territory values
  const [territory_val_onApply, set_territory_val_onApply] = useState([]);
  const [territory_val, set_territory_val] = useState([]);
  const onApplyTerritory = () => {
    set_territory_val_onApply(territory_val);
  };
  const onCancelTerritory = () => {
    set_territory_val(territory_val_onApply);
  };

  // radio for star values
  const star_initial_value = optionSTAR2 ? optionSTAR2[0] : null;
  const [star_val_onApply, set_star_val_onApply] = useState(star_initial_value);
  const [star_val, set_star_val] = useState(star_initial_value);
  const onApplyStar = () => {
    set_star_val_onApply(star_val);
  };
  const onCancelStar = () => {
    set_star_val(star_val_onApply);
  };

  // radio for channel values
  const channel_initial_value = optionChannelInfluence ? optionChannelInfluence[0] : null;
  const [channel_val_onApply, set_channel_val_onApply] = useState(channel_initial_value);
  const [channel_val, set_channel_val] = useState(channel_initial_value);
  const onApplyChannel = () => {
    set_channel_val_onApply(channel_val);
  };
  const onCancelChannel = () => {
    set_channel_val(channel_val_onApply);
  };

  // checkbox for lighthouse values
  const [lighthouse_val_onApply, set_lighthouse_val_onApply] = useState([]);
  const [lighthouse_val, set_lighthouse_val] = useState([]);
  const onApplyLighthouse = () => {
    set_lighthouse_val_onApply(lighthouse_val);
  };
  const onCancelLighthouse = () => {
    set_lighthouse_val(lighthouse_val_onApply);
  };



  // Axios call
  const fetchData = () => {
    let url = `${API_BASE_URL}/cross-border-trade/export.json`;
    // Uncomment only when API is now available and working
    // const token = localStorage.getItem('token');
    // let url = `${API_BASE}/orbis/api/totalrevenuegrowth`;
    // let defaultQueryParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}&showby=${group1_val_onApply}`;

    setIsLoading(true);

    axiosRetry(axios, { retries: 3 });

    axios({
      method: 'GET',
      url: url
      // Uncomment only when API is now available and working
      // url: url + defaultQueryParameters,
      // headers: {
      //   'Content-Type': 'application/json',
      //   "Authorization": `bearer ${token}`
      // }
    }).then(result => {
      const data = result.data;
      const resultSelected = data.segment_ranking;
      // Uncomment only if for API testing purposes
      // console.log(result.config.url);
      // setData(data);
      console.log(result.data)
      setData(result.data['import-region']);
      setImportData(result.data['import-region']);
      setExportData(result.data['export-region']);
      setCountryData(result.data['export-country']);
      setResultData(resultSelected);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setData(null);
      setIsLoading(false);
    });;

  };

  useEffect(() => {
    fetchData();
  }, []);

  // Filter Slider
  let sliderSettings = {
    slidesToShow: 5,
    slidesToScroll: 5,
    responsive: [
      {
        breakpoint: 7680,
        settings: 'unslick'
      },
      {
        breakpoint: 1776,
        settings: {
          arrows: true,
          swipe: true,
          slidesToShow: 4,
          slidesToScroll: 4,
        }
      },
      {
        breakpoint: 1508,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
        }
      },
      {
        breakpoint: 1349,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
        }
      }
    ]
  };
  let sliderData = [
    {
      key: uuidv4(),
      slide: <CheckboxDD
        title="HQ Region"
        id="hqregion"
        options={optionRegion}
        valueApplied={hqregion_val_onApply}
        checkVal={hqregion_val}
        setCheckVal={set_hqregion_val}
        onApply={onApplyHQRegion}
        onCancel={onCancelHQRegion}
      />
    }, {
      key: uuidv4(),
      slide: <SearchCheckboxDD
        title="HQ Country"
        id="hqcountry"
        options={optionCountry}
        checkVal={hqcountry_val}
        setCheckVal={set_hqcountry_val}
        onApply={onApplyHQCountry}
        onCancel={onCancelHQCountry}
      />
    }, {
      key: uuidv4(),
      slide: <CheckboxDD
        title="Vertical"
        id="vertical"
        options={optionVertical2}
        checkVal={vertical_val}
        setCheckVal={set_vertical_val}
        onApply={onApplyVertical}
        onCancel={onCancelVertical}
      />
    }, {
      key: uuidv4(),
      slide: <CheckboxDD
        title="Deploy Region"
        id="region"
        options={optionRegion}
        valueApplied={region_val_onApply}
        checkVal={region_val}
        setCheckVal={set_region_val}
        onApply={onApplyRegion}
        onCancel={onCancelRegion}
      />
    }, {
      key: uuidv4(),
      slide: <RadioDD
        id="gam"
        options={optionGAM}
        radioVal={gam_val}
        setRadioVal={set_gam_val}
        onApply={onApplyGAM}
        onCancel={onCancelGAM}
      />
    }, {
      key: uuidv4(),
      slide: <RadioDD
        title="Group by"
        id="group"
        options={optionGroupMethodology}
        radioVal={methodology_val}
        setRadioVal={set_methodology_val}
        onApply={onApplyMethodology}
        onCancel={onCancelMethodology}
        initialValue={methodology_initial_value}
      />
    }, {
      key: uuidv4(),
      slide: <RadioDD
        id="range"
        options={optionRange}
        radioVal={range_val}
        setRadioVal={set_range_val}
        onApply={onApplyRange}
        onCancel={onCancelRange}
        initialValue={range_initial_value}
      />
    }
  ]

  if (isLoading) return <Loader withContainer />;

  return (
    <>
      <div className={`page-content customer-content ${showComment ? 'open' : 'closed'}`}>
        {exportData ?
          exportData.customer_ranking.by_rank.data.length > 0 ?
            <>
              <FilterContainer
                showComment={showComment}
                onShowFilter={onShowFilter}
                onShowComment={onShowComment}
              >
                <div className="filters">
                  <FilterSlider
                    sliderSettings={sliderSettings}
                    sliderData={sliderData}
                  />
                </div>
                <FilterModal
                  title="Export"
                  setShowFilter={setShowFilter}
                  showFilter={showFilter}
                  onCloseFilter={onCloseFilter}
                  onApplyFilter={onApplyFilter}
                >
                  {/* PRIMARY FILTERS */}
                  <CheckboxDD
                    title="HQ Region"
                    id="hqregion-modal"
                    visibleInModal
                    options={optionRegion}
                    checkVal={hqregion_val}
                    setCheckVal={set_hqregion_val}
                  />
                  <SearchCheckboxDD
                    title="HQ Country"
                    id="hqcountry-modal"
                    options={optionCountry}
                    visibleInModal
                    checkVal={hqcountry_val}
                    setCheckVal={set_hqcountry_val}
                  />
                  <CheckboxDD
                    title="Vertical"
                    id="vertical-modal"
                    options={optionVertical2}
                    visibleInModal
                    checkVal={vertical_val}
                    setCheckVal={set_vertical_val}
                  />
                  <CheckboxDD
                    title="Deploy Region"
                    id="region-modal"
                    options={optionRegion}
                    visibleInModal
                    checkVal={region_val}
                    setCheckVal={set_region_val}
                  />
                  <RadioDD
                    title="GAM Flag"
                    id="gam-modal"
                    options={optionGAM}
                    visibleInModal
                    radioVal={gam_val}
                    setRadioVal={set_gam_val}
                    onApply={onApplyGAM}
                    onCancel={onCancelGAM}
                  />
                  {/* ALL FILTERS */}
                  <RadioDD
                    title="MNC Flag"
                    id="flag-modal"
                    visibleInModal
                    options={optionFlag}
                    radioVal={flag_val}
                    setRadioVal={set_flag_val}
                  />
                  <CheckboxDD
                    title="Company Size"
                    id="company-modal"
                    visibleInModal
                    options={optionCompanySize}
                    checkVal={region_val}
                    setCheckVal={set_region_val}
                  />
                  <RadioDD
                    title="Hyperscaler Flag"
                    id="hyperscaler-modal"
                    visibleInModal
                    options={optionHyperscaler}
                    radioVal={hyperscaler_val}
                    setRadioVal={set_hyperscaler_val}
                  />
                  <CheckboxDD
                    title="Lighthouse Flag"
                    id="lighthouse-modal"
                    visibleInModal
                    options={optionLighthouseFlag}
                    checkVal={lighthouse_val}
                    setCheckVal={set_lighthouse_val}
                  />
                  <SearchCheckboxDD
                    title="Deploy Country"
                    id="country-modal"
                    visibleInModal
                    options={optionCountry}
                    checkVal={country_val}
                    setCheckVal={set_country_val}
                    onApply={onApplyCountry}
                    onCancel={onCancelCountry}
                  />
                  <SearchCheckboxDD
                    title="Deploy Metro"
                    id="metro-modal"
                    options={optionMetro}
                    visibleInModal
                    checkVal={metro_val}
                    setCheckVal={set_metro_val}
                  />
                  <CheckboxDD
                    title="Sales Territory"
                    id="territory-modal"
                    visibleInModal
                    options={optionTerritory}
                    checkVal={territory_val}
                    setCheckVal={set_territory_val}
                  />
                  <RadioDD
                    title="STAR Flag"
                    id="star-flag-modal"
                    options={optionSTAR2}
                    radioVal={star_val}
                    setRadioVal={set_star_val}
                    onApply={onApplyStar}
                    onCancel={onCancelStar}
                    visibleInModal
                    initialValue={star_initial_value}
                  />
                  <RadioDD
                    title="Channel Influence"
                    id="channel-modal"
                    options={optionChannelInfluence}
                    visibleInModal
                    radioVal={channel_val}
                    setRadioVal={set_channel_val}
                    onApply={onApplyChannel}
                    onCancel={onCancelChannel}
                  />
                </FilterModal>
              </FilterContainer>
              <CustomerContent
                customerData={customerData}
                modalShow={modalShow}
                modalData={modalData}
                setModalShow={setModalShow}
                setModalData={setModalData}
                setReferenceID={setReferenceID}
                range_val_onApply={range_val_onApply.key}
                vertical_val_onApply={vertical_val_onApply.key}
              />
            </>
            : <ResourceNotFoundError redirectURL={"/executive-cockpit/customer-revenue/metro"} />
          : <InternalServerError redirectURL={"/executive-cockpit/customer-revenue/customer"} />}
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Export;
